import { TemplateRef } from '@angular/core';
import { SelectionType } from '@swimlane/ngx-datatable';

export type HtmlInputTypeEnum = 'text' | 'tel' | 'email' | 'date' | 'number' | 'checkbox';

export interface IRowDataType {
    id: any
}

export interface IComponentSettings {
    pageSize?: number;
    editable?: boolean;
    rmAsyncHandler?: (data: Array<IRowDataType>) => Promise<any>;
    addOrEditAsyncHandler?: (data: IRowDataType) => Promise<IRowDataType>;
}

export const noopPromise = (data: any) => new Promise((resolve, reject) => {
    resolve(data);
});

export const rmPromise = (data: Array<IRowDataType>) => new Promise((resolve, reject) => {
    resolve();
});

export const addOrEditPromise: (data: IRowDataType) => Promise<IRowDataType> = (data: IRowDataType) => new Promise((resolve, reject) => {
    const newData: IRowDataType = Object.assign(data, { id: new Date().getTime() });
    resolve(newData);
});


export const defaultSettings: IComponentSettings = {
    pageSize: 40,
    editable: false,
    rmAsyncHandler: rmPromise,
    addOrEditAsyncHandler: addOrEditPromise
}

export interface ITableColumnSpec {
    name?: string;
    prop?: string;
    width?: number;
    maxWidth?: number;
    minWidth?: number;
    canAutoResize?: boolean;
    draggable?: boolean;
    resizeable?: boolean;
    headerCheckboxable?: boolean;
    checkboxable?: boolean;
    sortable?: boolean;
    headerTemplate?: TemplateRef<any>;
    cellTemplate?: TemplateRef<any>;
    headerClass?: string;
    cellClass?: string;
    editable?: boolean; // editable, not part of the ngx datatable
    inputType?: HtmlInputTypeEnum;
    frozenLeft?: boolean;
    frozenRight?: boolean;
}

export interface ISortableColumn {
    prop: string;
    dir: 'asc' | 'desc';
}

export interface ISortChangedEvent {
    column: {
        prop: string;
    };
    newValue: 'asc' | 'desc';
}

export interface IPageChangedEvent {
    offset: number;
}

export interface IReactiveDatableBase<T extends IRowDataType> {
    settings: IComponentSettings;

    columns: Array<ITableColumnSpec>;

    rows: Array<T>;

    pageSize: number;
    loading: boolean;

    totalCount: number;
    pageIndex: number; // page index

    sortingKey: string;
    // Describe the init sorting order.
    sorts: Array<ISortableColumn>;
}

export interface ITableWithOperationsBase<T extends IRowDataType> extends IReactiveDatableBase<T> {

    // Support selected
    selected: Array<T>;
    // Suport editing
    editing: { [key: string]: boolean };
    backup: { [key: number]: T };

    anyEditing: boolean;

    onSelect(data: { selected: Array<T> }): void;
    startAdd(): void;
    startEdit(rowIndex: number): void;
    cancelEdit(rowIndex: number): void;
    confirmEditAsync(rowIndex: number): Promise<any>;
    updateValue(event: any, prop: string, rowIndex: number): void;
    cleanEditing(rowIndex: number): void;
    rmAsync(): Promise<any>;
}
